.Home {
  /* width: 375px; */
  width: 100%;
  /* height: 100%; */
}

.Home-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  /* width: 355px; */
  width: 100%;
  padding-left: 10px;
  padding-right: 10px;
}

.Home-ProfilePic {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  margin-right: 20px;
}

.Home-Categories {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.Home-Category {
  border-radius: 10px;
  margin: 10px;
  -webkit-box-shadow: 0 8px 20px -6px rgba(0, 0, 0, 0.8);
          box-shadow: 0 8px 20px -6px rgba(0, 0, 0, 0.8);
  padding: 30px;
  color:white;
  text-shadow: 1px 1px rgba(90, 88, 88, 0.294);
  text-decoration: none;
}

.Home-Category:hover {
  -webkit-transform: scale(1.01);
      -ms-transform: scale(1.01);
          transform: scale(1.01);
  cursor: pointer;
}

.Home-Category h2,
.Home-Category p {
  margin: 0;
}

.Home-Category p {
  font-weight: 100;
  line-height: 35px;
}

.Home-Movies {
  background: -webkit-gradient(linear, right top, left top, from(#1488cc), to(#2b32b2));
  background: -webkit-linear-gradient(right, #1488cc, #2b32b2);
  background: -o-linear-gradient(right, #1488cc, #2b32b2);
  background: linear-gradient(to left, #1488cc, #2b32b2);
}

.Home-Music {
  background: -webkit-gradient(linear, left top, right top, from(#11998e), to(#38ef7d));
  background: -webkit-linear-gradient(left, #11998e, #38ef7d);
  background: -o-linear-gradient(left, #11998e, #38ef7d);
  background: linear-gradient(to right, #11998e, #38ef7d);
}

.Home-Podcasts {
  background: -webkit-gradient(linear, right top, left top, from(#c33764), to(#1d2671));
  background: -webkit-linear-gradient(right, #c33764, #1d2671);
  background: -o-linear-gradient(right, #c33764, #1d2671);
  background: linear-gradient(to left, #c33764, #1d2671);
}

.Home-Books {
  background: -webkit-gradient(linear, left top, right top, from(#f2994a), to(#f2c94c));
  background: -webkit-linear-gradient(left, #f2994a, #f2c94c);
  background: -o-linear-gradient(left, #f2994a, #f2c94c);
  background: linear-gradient(to right, #f2994a, #f2c94c);
}

.Home-Movies--Pulse {
  -webkit-animation: pulse 0.75s infinite;
          animation: pulse 0.75s infinite;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1.00);
            transform: scale(1.00);
  }
  50% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
  }
  100% {
        -webkit-transform: scale(1.0);
                transform: scale(1.0);
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(1.00);
            transform: scale(1.00);
  }
  50% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
  }
  100% {
        -webkit-transform: scale(1.0);
                transform: scale(1.0);
  }
}


