.Movie {
  overflow-y: scroll;
  height: 100%;
}

.Movie-CloseButton-container {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  width: 100%;
  z-index: 10;
}

.Movie-CloseButton {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  /* /* float: right; */
  margin-top: 19px;
  margin-right: 12px;
  background-color: white;
}

.Movie-BackgroundImage-Container {
  width: 100%;
  height: 275px;
}

.Movie-BackgroundImage-Container::after {
  display: block;
  position: relative;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, rgba(255, 255, 255, 0)), to(#fff));
  background-image: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0, #fff 100%);
  background-image: -o-linear-gradient(top, rgba(255, 255, 255, 0) 0, #fff 100%);
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0, #fff 100%);
  margin-top: -300px;
  height: 300px;
  width: 100%;
  content: '';
}

.Movie-BackgroundImage{
  width: 100%;
  background-size: cover;
  -webkit-animation: 0.75s ease-out 0s 1 fadeIn;
          animation: 0.75s ease-out 0s 1 fadeIn;
}

.Movie-Thumbnail-Container {
  width: 100%;
  text-align: center;
  position: relative;
  bottom: 120px;
}

.Movie-Thumbnail-Image {
  width: 35%;

  /* box-shadow: 0 8px 20px -6px rgba(0, 0, 0, 0.8); */
-webkit-box-shadow: 10px 10px 71px 3px rgba(0,0,0,0.75);
        box-shadow: 10px 10px 71px 3px rgba(0,0,0,0.75);
}

.Movie-About-container {
  width: 80%;
  margin: auto;
  margin-top: -155px;
  text-align: center;
}

.Movie-title {
  font-size: 36px;
  margin-bottom: 0px;
  -webkit-animation: 0.25s ease-out 0s 1 scrollInFromBottom;
          animation: 0.25s ease-out 0s 1 scrollInFromBottom;
}

.Movie-year {
  margin: 0;
  -webkit-animation: 0.25s ease-out 0s 1 scrollInFromBottom;
          animation: 0.25s ease-out 0s 1 scrollInFromBottom;
}

.Movie-button {
  background-color: black;
  color: white;
  border-radius: 20px;
  height: 45px;
  line-height: 30px;
  font-size: 16px;
  padding-left: 25px;
  padding-right: 25px;
  margin-top: 20px;
  -webkit-animation: 0.35s ease-out 0s 1 scrollInFromBottom;
          animation: 0.35s ease-out 0s 1 scrollInFromBottom;
}

.Movie-abstract {
  text-align: left;
  font-size: 15px;
  line-height: 19px;
  -webkit-animation: 0.45s ease-out 0s 1 scrollInFromBottom;
          animation: 0.45s ease-out 0s 1 scrollInFromBottom;
}


@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes scrollInFromBottom {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@keyframes scrollInFromBottom {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}


