.App-Screen {
  width: 100%;
  height: 100vh;

  /* This is done so other relative items can stick based off this */
  position: relative;
}

  @media screen and (min-width: 400px) {
    .App-Screen {
      width: 375px;
      height: 667px;
      margin: 50px;
      -webkit-box-shadow: 5px 5px 50px #CDCDCD;
              box-shadow: 5px 5px 50px #CDCDCD;
    }
  }


