.Movies {
  height: 100%;
  /* background: linear-gradient(to bottom, #04b5eb, #146fe7); */
  color: white;
  padding-left: 10px;
}

.Movies-Background {
  background: -webkit-gradient(linear, left top, left bottom, from(#04b5eb), to(#146fe7));
  background: -webkit-linear-gradient(top, #04b5eb, #146fe7);
  background: -o-linear-gradient(top, #04b5eb, #146fe7);
  background: linear-gradient(to bottom, #04b5eb, #146fe7);
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -1;
  left: 0px;
  top: 0;
  bottom: 0;
  margin: auto;
  -webkit-animation: 0.3s ease-out 0s 1 expandHeight;
          animation: 0.3s ease-out 0s 1 expandHeight;
}

.Movies-ItemsCount {
  margin-top: -15px;
}

.Movies-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  /* width: 355px; */
  width: 100%;
  padding-right: 10px;
}

.Movies-CloseButton {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  background-color: white;
  margin-right: 12px;
  margin-top: 2px;
}

.Movies-list-container {
  width: 100%;
  white-space: nowrap;

}
.Movies-list-view {
  /* width: 350px; */
  width: 95%;
  overflow-x: scroll;

overflow-y: hidden;
}

.Movies-thumbnail {
  display: inline;
  width: 120px;
  height: 180px;
  margin-right: 15px;
  -webkit-box-shadow: 0 8px 20px -6px rgba(0, 0, 0, 0.8);
          box-shadow: 0 8px 20px -6px rgba(0, 0, 0, 0.8);
  -webkit-animation: 0.5s ease-out 0s 1 slideInFromRight;
          animation: 0.5s ease-out 0s 1 slideInFromRight;
}

.Movies-thumbnail:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
  transition: -webkit-transform 0.5s ease-in-out;
  -o-transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
  cursor: pointer;
}

@-webkit-keyframes slideInFromRight {
  0% {
    -webkit-transform: translateX(200%);
            transform: translateX(200%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes slideInFromRight {
  0% {
    -webkit-transform: translateX(200%);
            transform: translateX(200%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes expandHeight {
  0% {
    height: 0%;
  }
  100% {
    height: 100%;
  }
}

@keyframes expandHeight {
  0% {
    height: 0%;
  }
  100% {
    height: 100%;
  }
}


